<script setup> 
import { ref, computed, onMounted, onUnmounted } from "vue";
import axios from "axios";
import moment from 'moment';

var tasks = ref([]);
var selectedDateTime =[];
var remarks="";
var curr_date=ref();

Tasks(0);
function Tasks(task_num) {

  if(task_num == 0) {
    var temp_date = ref(new Date());
    curr_date.value = getFormattedDate(temp_date);
  } 
  if(task_num == 1) {
    var temp_date = ref(new Date());
    curr_date.value = getFormattedDateNext(temp_date);
  }
  if(task_num == -1) {
    var temp_date = ref(new Date());
    curr_date.value = getFormattedDatePrev(temp_date);
  }


  tasks = ref([]);
  axios
      .get('/tasks', {
      params: {
        task_date_num: task_num,
        }
      }) 
      .then(response => {
        var temp = response.data;
        console.log(temp);
        let i = 0;
        for (let item of temp) {
            i=i+1;
            item.task_id = i;
            tasks.value.push(item);
        }
        console.log(tasks);
        
      })
}

function getFormattedDate(temp_date) {
    return moment(temp_date).format("DD-MM-YYYY")
}

function getFormattedDatePrev(temp_date) {
    return moment(temp_date).subtract(1,'days').format("DD-MM-YYYY")
}

function getFormattedDateNext(temp_date) {
    return moment(temp_date).add(1,'days').format("DD-MM-YYYY")
}

function TasksNext() {
  tasks.value = null;

  Tasks(1);
}

function TasksPrev() {
  tasks.value = null;
  
  Tasks(-1);
}




</script>
<template>

<div class="flex flex-wrap">
    <button class="bg-yellow-500 block w-1/6 py-5 font-semibold" @click="TasksPrev()">PREVIOUS DAY SCHEDULE</button>  
        <div class="block w-4/6 py-2 text-center font-semibold text-4xl">
            DATE: {{ curr_date}}
         </div>
    <button class="bg-yellow-500 w-1/6 py-5 font-semibold" @click="TasksNext()">NEXT DAY SCHEDULE</button>  
</div>
    

    <div class="overflow-x-auto">   
    <table class="w-full text-sm  text-center text-gray-800">
      <thead class="text-3xl text-yellow-800 uppercase bg-green-500">
        <tr class="">
          <th scope="col" c lass="px-10 py-3">
            S.No
          </th>
          <th scope="col" class="px-10 py-3">
            TIME
          </th>
          <th scope="col" class="px-20 py-3">
             SUBJECT
          </th>
          <th scope="col" class="px-20 py-3">
             REMARKS
          </th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="task in tasks" :class=" [ {'even-group': task.task_id % 2 == 0},
                                            {'odd-group': task.task_id % 2 != 0} ]">
            <td class="px-10 py-3">
                {{ task.task_id }}
             </td>
          <td class="px-10 py-3">
            {{ task.task_time }}
          </td>
          <td class="px-20 py-3">
            {{ task.task_subject }}
          </td>
          <td class="px-20 py-3">
            {{ task.task_remarks }}
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<style>

tr.odd-group {
  background: rgb(215, 215, 215);
  border-bottom-width: 1px;
  color: rgb(54, 69, 79);
  /* font-weight: bold; */
  font-size: 20px;
  /* bg-gray-50 border text-center text-xl font-semibold text-gray-800 */
}   
tr.even-group {
  background: rgb(135, 206, 250);
  border-bottom-width: 1px;
  color: rgb(54, 69, 79);
  /* font-weight: bold; */
  font-size: 20px;
}
</style>