<script setup>
import axios from "axios";
import { ref } from "vue";


var isStartError = ref(false);
var startError = ref("");
var isStartSuccess = ref(false);
var startSuccess = ref("");

  function createTasks(subject,datetime,remarks) {

  isStartError.value = false;
  startError.value = "";
  isStartSuccess.value = false;
  startSuccess.value = "";

    var subject;
    var datetime;
    var remarks;

    var request = {
      task_subject: subject,
      task_time: datetime,
      task_remarks: remarks
  };

  console.log(request);

  axios.post('/create', request)
    .then(function (response) {
      console.log(response);
      
      if(response.data.errorCode != null && response.data.errorCode != '') {
            startError.value = response.data.errorMsg;
            isStartError.value = true;
            return;
        }

      isStartSuccess.value = true;
      startSuccess.value = "Task Created";
    })
    .catch(function (error) {
      startError.value = "Error in creating task. Please try again later";
      isStartError.value = true;
    });
  }
</script>
<template>
  
    <header class="text-center font-bold text-6xl mt-10">NLC INDIA LIMITED</header>
    <header class="text-center font-bold text-3xl mt-10">CREATE TASKS</header>
  
     <div class="flex flex-wrap mt-14 text-center">
          <div class="w-2/6"></div>
          <!-- <div class="w-1/6"></div> -->
          <label for="Subject" class="block w-1/6 text-xl font-medium leading-6">Subject</label>
          <input id="subject" name="subject" type="text" v-model="subject" class="block w-1/6 rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6" />  
      </div>  
      <div class="flex flex-wrap mt-14 text-center">
          <div class="w-2/6"></div>
          <label for="Date and Time" class="block w-1/6 text-xl font-medium leading-6">Date and Time </label>
          <input id="datetime" name="username" type="datetime-local" v-model="datetime" class="block w-1/6 rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6" />
      </div>   
      <div class="flex flex-wrap mt-14 text-center">
          <div class="w-2/6"></div>
          <label for="Remarks" class="block w-1/6 text-xl font-medium leading-6">Remarks </label>
          <input id="remarks" name="username" type="text" v-model="remarks" class="block w-1/6 rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6" />
      </div> 
      <div class="flex flex-wrap mt-14 text-center">
        <div class="w-3/6"></div>
        <button @click = "createTasks(subject,datetime,remarks)" class="justify-center rounded-md bg-gray-600 px-3 py-1.5 text-xl font-semibold leading-6 text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"> Create </button>
        <div class="w-1/3"></div>
      </div>

      <div v-if="isStartError" class="bg-red-100 border-red-400 text-red-700 mt-10"
      role="alert"> 
      <strong class="font-bold">Error! </strong>
      <span class="block sm:inline">{{ startError }}</span>
    </div>

    <div v-if="isStartSuccess" class="text-center text-green-700 mt-10 border-green-400"
      role="alert">
      <strong class="font-bold">Success! </strong>
      <span class="block sm:inline">{{ startSuccess }}</span>
    </div>
      
</template>